import jsPDF from "jspdf";
import moment from "moment";

const StudentBonafideCertificate = (student, institute, otherImageUrl) => {
  var doc = new jsPDF({
    format: "a4",
    putOnlyUsedFonts: true,
  });

  var width = doc.internal.pageSize.getWidth();
  var height = doc.internal.pageSize.getHeight();
  const todayDate = new Date();

  var firstPageHeight = 0;

  if (student?.letterHead) {
    firstPageHeight = 45;
  } else {
    if (institute.instituteImage) {
      doc.addImage(institute.instituteImage, "JPEG", 10, 3, 25, 25);
    }
    if (institute.affiliatedImage) {
      doc.addImage(institute.affiliatedImage, "JPEG", width - 34, 3, 25, 25);
    }
    doc.setLineWidth(8);
    doc.setDrawColor(255, 255, 255);
    doc.circle(22.4, 15.3, 16);
    doc.circle(width - 21.4, 15.3, 16);
    doc.setLineWidth(0);
    firstPageHeight = institute?.insAffiliated ? 17 : 10;
    const instituteName = doc.splitTextToSize(institute?.insName ?? "", 130);
    doc.setFont("700");
    doc.setTextColor(18, 18, 18);
    doc.setFontSize(16);
    doc.text(107, firstPageHeight, instituteName, "center");
    if (instituteName?.length) {
      for (let i = 0; i < instituteName?.length; i++) {
        firstPageHeight += 6;
      }
    }
    if (institute?.insAffiliated) {
      doc.setFontSize(10);
      doc.text(107, 10, institute?.insAffiliated ?? "", "center");
    }

    doc.setFont("Arial", "normal", "400");
    doc.setTextColor(18, 18, 18);
    doc.setFontSize(10);
    const addressSplit = doc.splitTextToSize(institute?.insAddress ?? "", 140);
    doc.text(width / 2, firstPageHeight, addressSplit, "center");

    if (addressSplit?.length) {
      for (let i = 0; i < addressSplit?.length; i++) {
        firstPageHeight += 5;
      }
    }
    doc.text(
      width / 2,
      firstPageHeight + 1,
      `Mob No : ${institute?.insPhoneNumber ?? ""} , Mail: ${
        institute?.insEmail ?? ""
      }`,
      "center"
    );
    firstPageHeight += 10;
  }
  // doc.setLineWidth(0.3);
  // doc.setDrawColor(18, 18, 18);
  // doc.line(7, 2, width - 7, 2);

  doc.setFont("Inter", "italic", "400");
  doc.setTextColor(18, 18, 18);
  doc.setFontSize(16);
  doc.text(
    width / 2,
    firstPageHeight,
    "Bona-fide Certificate",
    null,
    null,
    "center"
  );
  //   firstPageHeight += 2;
  let spaces = doc.getTextWidth("Bona-fide Certificate");
  doc.setLineWidth(0.3);
  doc.setDrawColor(137, 137, 137);
  doc.line(
    width / 2 - spaces,
    firstPageHeight,
    width / 2 - spaces / 2 - 2,
    firstPageHeight
  );

  doc.setDrawColor(137, 137, 137);
  doc.setLineWidth(0.3);
  doc.line(
    width / 2 + spaces / 2 + 2,
    firstPageHeight,
    width / 2 + spaces,
    firstPageHeight
  );

  firstPageHeight += 10;

  doc.setTextColor(0, 0, 0);
  doc.setFontSize(11);

  doc.setFont("Inter", "normal", "700");
  doc.text(10, firstPageHeight - 6, "Date: ");
  doc.setFont("Inter", "normal", "600");
  doc.text(
    20,
    firstPageHeight - 6,
    `${moment(todayDate).format("DD-MM-yyyy")}`
  );

  doc.setFont("Inter", "normal", "700");
  doc.text(10, firstPageHeight - 1, "Certificate No.: ");
  doc.setFont("Inter", "normal", "600");
  doc.text(38, firstPageHeight - 1, `${student?.certificateNumber ?? "1"}`);
  firstPageHeight += 4;

  if (student.studentImage) {
    doc.addImage(
      student.studentImage,
      "JPEG",
      width - 40,
      firstPageHeight - 14,
      30,
      30
    );
  }
  if (student?.grNumber) {
    doc.setFont("Inter", "italic", "700");
    doc.setFontSize(12);
    doc.text(
      width - 10 - doc.getTextWidth(`Gr No: ${student?.grNumber ?? ""}`),
      firstPageHeight + 20,
      `Gr No: ${student?.grNumber ?? ""}`,
      {
        align: "left",
      }
    );
  }

  doc.setFont("Inter", "normal", "700");
  doc.setFontSize(11);
  doc.text(10, firstPageHeight, "Subject/Reason:  ");

  doc.setFont("Inter", "normal", "600");

  let reasonSplit = doc.splitTextToSize(student?.reason ?? "", width - 135);
  doc.text(40, firstPageHeight, reasonSplit, "left");

  firstPageHeight += 20;
  let showingContent = "";

  if (student?.print_content) {
    showingContent = student?.print_content;
  } else {
    showingContent = `This is to certify that Mr./Miss @${
      ` ${student?.name}` ?? ""
    }@, Birth date as per records of institute is @${
      student?.dob ? student?.dob : "N/A"
    }@, is a bona-fide student of this institute, studying in class @${
      ` ${student?.classTitle}` ?? "N/A"
    }, ${student?.dName ?? "N/A"}, ${student?.batchName ?? "N/A"}@.`;
  }

  let showingContentSplit = doc.splitTextToSize(showingContent, width - 35);

  let count = 1;
  for (let content of showingContentSplit) {
    let c_split = content.split("@");
    for (let i = 0; i < c_split?.length; i++) {
      let s_width = count === 1 ? 32 : 10;
      for (let j = 0; j < i; j++) {
        if (c_split[j] === "N/A") {
          s_width += doc.getTextWidth(c_split[j]) + 1;
        } else {
          s_width += doc.getTextWidth(c_split[j]) - 1;
        }
      }
      if (i % 2 === 0) {
        doc.setFont("Inter", "normal", "600");
        doc.text(s_width, firstPageHeight + 5, c_split[i]);
      } else {
        doc.setFont("Inter", "normal", "700");
        doc.text(s_width + 2, firstPageHeight + 5, c_split[i]);
      }
    }
    count = 2;
    firstPageHeight += 5;
  }

  if (student?.message) {
    firstPageHeight += 6; //140
    doc.setFont("Inter", "normal", "600");
    doc.setTextColor(0, 0, 0);
    doc.setFontSize(11);
    if (!student?.letterHead && institute?.autority_stamp_profile) {
      doc.setLineWidth(8);
      doc.setDrawColor(255, 255, 255);
      doc.circle(23.9, firstPageHeight + 17, 16);
    }
    doc.text(10, firstPageHeight, `Note : ${student?.message}`, "left");
  }
  if (otherImageUrl) {
    firstPageHeight += 6;
    doc.addImage(otherImageUrl, "JPEG", 10, firstPageHeight, 190, 90);
    firstPageHeight += 98;
  }

  firstPageHeight += 5;
  if (student?.letterHead) {
  } else {
    if (institute?.autority_stamp_profile) {
      firstPageHeight += 11;
      doc.addImage(
        institute.autority_stamp_profile,
        "JPEG",
        12,
        firstPageHeight - 13,
        25,
        25
      );
    }

    doc.setFont("Inter", "bold", "700");
    doc.setTextColor(0, 0, 0);
    doc.setFontSize(12);
    doc.text(10, firstPageHeight + 20, "Stamp Of Institute");
  }

  doc.setFont("Inter", "normal", "700");
  doc.setTextColor(0, 0, 0);
  doc.setFontSize(12);
  if (institute?.authority_signature) {
    doc.addImage(
      institute.authority_signature,
      "JPEG",
      width - 50,
      firstPageHeight - 13,
      40,
      25
    );
  }
  doc.text(
    width -
      10 -
      doc.getTextWidth(
        student?.authority
          ? `${student?.authority}:`
          : "Principal/Competent Authority:"
      ),
    firstPageHeight + 20,
    student?.authority
      ? `${student?.authority}:`
      : "Principal/Competent Authority:"
  );

  // if (student?.isOriginal) {
  // } else {
  //   doc.setTextColor(150);
  //   doc.text(100, firstPageHeight + 20, `Qviple Copy`);
  // }

  // doc.setLineWidth(0.3);
  // doc.setDrawColor(18, 18, 18);
  // doc.line(7, 1.85, 7, firstPageHeight + 25.15);
  // doc.line(width - 7, 1.85, width - 7, firstPageHeight + 25.15);
  // doc.line(7, firstPageHeight + 25, width - 7, firstPageHeight + 25);

  if (institute?.is_dublicate_bonafide === "Yes") {
    let borderHeight = 0;
    if (firstPageHeight > 148) {
      firstPageHeight += 25;
      borderHeight = firstPageHeight + 2;
    } else {
      firstPageHeight = Math.floor(height / 2);
      firstPageHeight = +firstPageHeight;
      borderHeight = firstPageHeight + 2;
    }

    // ==================================

    if (student?.letterHead) {
      firstPageHeight = 45;
    } else {
      if (institute.instituteImage) {
        doc.addImage(
          institute.instituteImage,
          "JPEG",
          10,
          firstPageHeight + 3,
          25,
          25
        );
      }

      if (institute.affiliatedImage) {
        doc.addImage(
          institute.affiliatedImage,
          "JPEG",
          width - 34,
          firstPageHeight + 3,
          25,
          25
        );
      }

      doc.setLineWidth(8);
      doc.setDrawColor(255, 255, 255);
      doc.circle(22.4, firstPageHeight + 15.3, 16);
      doc.circle(width - 21.4, firstPageHeight + 15.3, 16);
      doc.setLineWidth(0);
      if (institute?.insAffiliated) {
        doc.setFont("Arial", "normal", "400");
        doc.setFontSize(10);
        doc.text(
          107,
          firstPageHeight + 10,
          institute?.insAffiliated ?? "",
          "center"
        );
      }

      firstPageHeight = institute?.insAffiliated
        ? firstPageHeight + 17
        : firstPageHeight + 10;
      const instituteName = doc.splitTextToSize(institute?.insName ?? "", 130);
      doc.setFont("700");
      doc.setTextColor(18, 18, 18);
      doc.setFontSize(16);
      doc.text(107, firstPageHeight, instituteName, "center");
      if (instituteName?.length) {
        for (let i = 0; i < instituteName?.length; i++) {
          firstPageHeight += 6;
        }
      }

      doc.setFont("Arial", "normal", "400");
      doc.setTextColor(18, 18, 18);
      doc.setFontSize(10);
      const addressSplit = doc.splitTextToSize(
        institute?.insAddress ?? "",
        140
      );
      doc.text(width / 2, firstPageHeight, addressSplit, "center");

      if (addressSplit?.length) {
        for (let i = 0; i < addressSplit?.length; i++) {
          firstPageHeight += 5;
        }
      }
      doc.text(
        width / 2,
        firstPageHeight + 1,
        `Mob No : ${institute?.insPhoneNumber ?? ""} , Mail: ${
          institute?.insEmail ?? ""
        }`,
        "center"
      );
      firstPageHeight += 10;
    }

    // doc.setLineWidth(0.3);
    // doc.setDrawColor(18, 18, 18);
    // doc.line(7, borderHeight, width - 7, borderHeight);

    doc.setFont("Inter", "italic", "400");
    doc.setTextColor(18, 18, 18);
    doc.setFontSize(16);
    doc.text(
      width / 2,
      firstPageHeight,
      "Bona-fide Certificate",
      null,
      null,
      "center"
    );
    //   firstPageHeight += 2;
    doc.setLineWidth(0.3);
    doc.setDrawColor(137, 137, 137);
    doc.line(
      width / 2 - spaces,
      firstPageHeight,
      width / 2 - spaces / 2 - 2,
      firstPageHeight
    );

    doc.setDrawColor(137, 137, 137);
    doc.setLineWidth(0.3);
    doc.line(
      width / 2 + spaces / 2 + 2,
      firstPageHeight,
      width / 2 + spaces,
      firstPageHeight
    );

    firstPageHeight += 10;

    doc.setTextColor(0, 0, 0);
    doc.setFontSize(11);

    doc.setFont("Inter", "normal", "700");
    doc.text(10, firstPageHeight - 6, "Date: ");
    doc.setFont("Inter", "normal", "600");
    doc.text(
      20,
      firstPageHeight - 6,
      `${moment(todayDate).format("DD-MM-yyyy")}`
    );

    doc.setFont("Inter", "normal", "700");
    doc.text(10, firstPageHeight - 1, "Certificate No.: ");
    doc.setFont("Inter", "normal", "600");
    doc.text(38, firstPageHeight - 1, `${student?.certificateNumber ?? "1"}`);
    firstPageHeight += 4;

    if (student.studentImage) {
      doc.addImage(
        student.studentImage,
        "JPEG",
        width - 40,
        firstPageHeight - 14,
        30,
        30
      );
    }
    if (student?.grNumber) {
      doc.setFont("Inter", "italic", "700");
      doc.setFontSize(12);
      doc.text(
        width - 10 - doc.getTextWidth(`Gr No: ${student?.grNumber ?? ""}`),
        firstPageHeight + 20,
        `Gr No: ${student?.grNumber ?? ""}`,
        {
          align: "left",
        }
      );
    }

    doc.setFont("Inter", "normal", "700");
    doc.setFontSize(11);
    doc.text(10, firstPageHeight, "Subject/Reason:  ");

    doc.setFont("Inter", "normal", "600");

    doc.text(40, firstPageHeight, reasonSplit, "left");

    firstPageHeight += 20;

    count = 1;
    for (let content of showingContentSplit) {
      let c_split = content.split("@");
      for (let i = 0; i < c_split?.length; i++) {
        let s_width = count === 1 ? 32 : 10;
        for (let j = 0; j < i; j++) {
          if (c_split[j] === "N/A") {
            s_width += doc.getTextWidth(c_split[j]) + 1;
          } else {
            s_width += doc.getTextWidth(c_split[j]) - 1;
          }
        }
        if (i % 2 === 0) {
          doc.setFont("Inter", "normal", "600");
          doc.text(s_width, firstPageHeight + 5, c_split[i]);
        } else {
          doc.setFont("Inter", "normal", "700");
          doc.text(s_width + 2, firstPageHeight + 5, c_split[i]);
        }
      }
      count = 2;
      firstPageHeight += 5;
    }

    if (student?.message) {
      firstPageHeight += 6; //140
      doc.setFont("Inter", "normal", "600");
      doc.setTextColor(0, 0, 0);
      doc.setFontSize(11);
      if (!student?.letterHead && institute?.autority_stamp_profile) {
        doc.setLineWidth(8);
        doc.setDrawColor(255, 255, 255);
        doc.circle(23.9, firstPageHeight + 17, 16);
      }
      doc.text(10, firstPageHeight, `Note : ${student?.message}`, "left");
    }
    if (otherImageUrl) {
      firstPageHeight += 6;
      doc.addImage(otherImageUrl, "JPEG", 10, firstPageHeight, 190, 90);
      firstPageHeight += 98;
    }

    firstPageHeight += 5;
    if (student?.letterHead) {
    } else {
      if (institute?.autority_stamp_profile) {
        firstPageHeight += 11;
        doc.addImage(
          institute.autority_stamp_profile,
          "JPEG",
          12,
          firstPageHeight - 13,
          25,
          25
        );
      }

      doc.setFont("Inter", "bold", "700");
      doc.setTextColor(0, 0, 0);
      doc.setFontSize(12);
      doc.text(10, firstPageHeight + 20, "Stamp Of Institute");
    }

    doc.setFont("Inter", "normal", "700");
    doc.setTextColor(0, 0, 0);
    doc.setFontSize(12);
    if (institute?.authority_signature) {
      doc.addImage(
        institute.authority_signature,
        "JPEG",
        width - 50,
        firstPageHeight - 13,
        40,
        25
      );
    }
    doc.text(
      width -
        10 -
        doc.getTextWidth(
          student?.authority
            ? `${student?.authority}:`
            : "Principal/Competent Authority:"
        ),
      firstPageHeight + 20,
      student?.authority
        ? `${student?.authority}:`
        : "Principal/Competent Authority:"
    );

    // if (student?.isOriginal) {
    // } else {
    //   doc.setTextColor(150);
    //   doc.text(100, firstPageHeight + 20, `Qviple Copy`);
    // }

    // doc.setLineWidth(0.3);
    // doc.setDrawColor(18, 18, 18);
    // doc.line(7, borderHeight - 0.15, 7, firstPageHeight + 25.15);
    // doc.line(
    //   width - 7,
    //   borderHeight - 0.15,
    //   width - 7,
    //   firstPageHeight + 25.15
    // );
    // doc.line(7, firstPageHeight + 25, width - 7, firstPageHeight + 25);
  }

  return doc.output("blob");
  // return doc.output("bloburl", {
  //   filename: `${student?.name}-Bona-fide.pdf`,
  // });
  // doc.output("dataurlnewwindow", {
  //   filename: `${student?.name}-Bona-fide.pdf`,
  // });
};

export default StudentBonafideCertificate;
