// import StudentBonafidePdf from "../StudentBonafidePdf";
import StudentBonafideCertificate from "../certificate/StudentBonafideCertificate";
import StudentLeavingnCertificate from "../certificate/StudentLeavingnCertificate";
import StudentLeavingnCertificate2 from "../certificate/StudentLeavingnCertificate2";
import StudentLeavingnCertificate3 from "../certificate/StudentLeavingnCertificate3";

onmessage = (message) => {
  let args = message.data;
  let printResult = "";

  if (args?.printCertificateType === "BONAFIDE") {
    printResult = StudentBonafideCertificate(
      args?.studentInfo,
      args?.institute,
      args?.otherImageUrl
    );
  } else if (args?.printCertificateType === "LEAVING") {
    printResult = StudentLeavingnCertificate(
      args?.institute,
      args?.studentInfo
    );
  } else if (args?.printCertificateType === "LEAVING_2") {
    printResult = StudentLeavingnCertificate2(
      args?.institute,
      args?.studentInfo
    );
  } else if (args?.printCertificateType === "LEAVING_3") {
    printResult = StudentLeavingnCertificate3(
      args?.institute,
      args?.studentInfo
    );
  } else if (args?.printCertificateType === "TRANSFER") {
  } else {
    printResult = "Not Indetified Certificate Flow";
  }

  postMessage(printResult);
};
